module.exports = "/* -----search box----- */\r\n\r\nbody {\r\n  padding-top: 75px;\r\n}\r\n\r\n.search-container {\r\n  width: 490px;\r\n  display: block;\r\n  margin: 0 auto;\r\n}\r\n\r\ninput#search-bar {\r\n  margin: 0 auto;\r\n  width: 100%;\r\n  height: 45px;\r\n  padding: 0 20px;\r\n  font-size: 1rem;\r\n  border: 1px solid #D0CFCE;\r\n  outline: none;\r\n  border-radius: 20px;\r\n  &:focus {\r\n    border: 1px solid #008ABF;\r\n    transition: 0.35s ease;\r\n    color: #008ABF;\r\n    &::-webkit-input-placeholder {\r\n      transition: opacity 0.45s ease;\r\n      opacity: 0;\r\n    }\r\n    &::-moz-placeholder {\r\n      transition: opacity 0.45s ease;\r\n      opacity: 0;\r\n    }\r\n    &:-ms-placeholder {\r\n      transition: opacity 0.45s ease;\r\n      opacity: 0;\r\n    }\r\n  }\r\n}\r\n\r\n.search-icon {\r\n  background-color: Transparent;\r\n  background-repeat: no-repeat;\r\n  border: none;\r\n  cursor: pointer;\r\n  overflow: hidden;\r\n  outline: none;\r\n  position: relative;\r\n  float: right;\r\n  width: 30px;\r\n  height: 30px;\r\n  top: -32px;\r\n  right: -35px;\r\n}\r\n\r\n#submitButton {\r\n  background-color: Transparent;\r\n  background-repeat: no-repeat;\r\n  border: none;\r\n  cursor: pointer;\r\n  overflow: hidden;\r\n  outline: none;\r\n  position: relative;\r\n  float: right;\r\n  width: 30px;\r\n  height: 30px;\r\n  top: -38px;\r\n  right: 10px;\r\n}\r\n\r\n.center {\r\n  width: 500px;\r\n  height: 200px;\r\n  padding: 10px;\r\n  position: absolute;\r\n  margin: auto;\r\n  top: 0;\r\n  right: 0;\r\n  bottom: 0;\r\n  left: 0;\r\n  border-radius: 3px;\r\n}\r\n\r\n/* chips */\r\n\r\n.fa {\r\n  padding-left: 10px;\r\n  color: #888;\r\n  font-weight: bold;\r\n  float: right;\r\n  font-size: 20px;\r\n  cursor: pointer;\r\n}\r\n\r\n.fa:hover {\r\n  color: #888;\r\n}\r\n\r\n.btn-div {\r\n  width: 100%;\r\n  display: flex;\r\n  justify-content: center;\r\n  align-items: center;\r\n}\r\n\r\n.btn btn-primary {\r\n  text-align: center;\r\n}\r\n